@import "components/m_header";
@import "components/m_asside";
@import "components/m_footer";

@media screen and (max-width: 1200px), screen and (max-device-width: 1200px){
  body{
    .content{
      .container{
        .row{
          .col-md-9{
            .shoppingCart{
              &__securePayment{
                &__headline{
                  form{
                    ul{
                      .state{
                        label{
                          input{
                            width: 62%;
                          }
                        }
                      }
                    }
                  }
                }
              }
              &__discount{
                &__coupon,
                &__certificate{
                  ul{
                    li{
                      input{
                        width: 370px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px), screen and (max-device-width: 992px){
  body{
    .content{
      .container{
        .row{
          .col-md-3{
            .aside{
              display: none;
            }
          }
          .col-sm-12{
            .aside{
              display: block;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 770px), screen and (max-device-width: 770px){
  body{
    .content{
      .container{
        .row{
          .col-md-9{
            .shoppingCart{
              &__discount{
                &__coupon,
                &__certificate{
