}

@media screen and (max-width: 710px), screen and (max-device-width: 710px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .shoppingCart{
              &__securePayment{
                &__headline{
                  form{
                    ul{
                      width: 100%;
                      li{
                        p{
                          display: none;
                        }
                        label{
                          width: 100%;
                          ::-webkit-input-placeholder{
                            color: #525252;
                          }
                          ::-moz-placeholder{
                            color: #525252;
                          }
                          :-moz-placeholder{
                            color: #525252;
                          }
                          :-ms-input-placeholder{
                            color: #525252;
                          }
                        }
                      }
                      .state{
                        label{
                          select{
                            margin: 0 10px 0 0;
                          }
                          input{
                            width: 85.5%;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

